/*city modal*/
.modal-city{
  display: none;
  position: fixed;
  width: 630px;
  background-color: #ffffff;
  border-radius: 5px;
  top: 25%;
  margin-left: calc(50% - (630px / 2));
  opacity: 0; /* пoлнoстью прoзрaчнo для aнимирoвaния */
  z-index: 5; /* oкнo дoлжнo быть нaибoлее бoльшем слoе */
  padding-top: 36px;
  @include border-box;
  @include breakpoint(max-width 600px){
    width: 100%;
    margin-left: 0;
  }
  &:after{
    display: block;
    position: absolute;
    content: '';
    width: 99px;
    height: 57px;
    right: 9px;
    bottom: 0;
    background-image: url('../i/OAMI-logo.svg');
    background-repeat: no-repeat;
    -webkit-background-size: cover;
    background-size: cover;
  }
  .title{
    display: block;
    position: relative;
    float: left;
    width: calc(100% - (50px*2));
    margin-top: 0;
    margin-bottom: 37px;
    margin-left: 50px;
    margin-right: 50px;
    padding-bottom: 20px;
    @include box-sizing(border-box);
    color: #0e0e0e;
    font-size: 18px;
    font-weight: 600;
    border-bottom: 1px solid #dddddd;
    &:after{
      display: block;
      position: absolute;
      content: '';
      left: 0;
      bottom: -1px;
      width: 70px;
      height: 2px;
      background-color: #c12025;
    }
  }
  .subtitle{
    display: block;
    position: relative;
    float: left;
    width: calc(100% - (50px*2));
    margin-top: 0;
    margin-bottom: 37px;
    margin-left: 50px;
    margin-right: 50px;
    color: #0e0e0e;
    font-size: 14px;
    font-weight: 300;
    line-height: 1.4;
    > span{
      font-weight: 600;
    }
  }
  .element{
    display: block;
    position: relative;
    float: left;
    width: 100%;
    /*padding: 0 50px;*/
    @include box-sizing(border-box);
    margin-bottom: 9px;
    &:last-of-type{
      margin-bottom: 36px;
    }
    .title{
      display: block;
      position: relative;
      float: left;
      width: 50%;
      margin: 0;
      padding: 0;
      border: none;
      text-align: left;
      color: #8e8d8d;
      font-size: 15px;
      font-weight: 300;
      line-height: 18px;
      &:after{
        display: none;
      }
    }
    .value{
      display: block;
      position: relative;
      float: left;
      width: 50%;
      margin: 0;
      text-align: right;
      color: #0e0e0e;
      font-size: 15px;
      font-weight: 300;
      line-height: 18px;
    }
  }
  &__close{
    display: block;
    position: absolute;
    content: '';
    top: 36px;
    right: 50px;
    width: 12px;
    height: 12px;
    background-image: url('../i/icons/modal-close-icon.png');
    background-repeat: no-repeat;
    -webkit-background-size: contain;
    background-size: contain;
    cursor: pointer;
  }
  &__form{
    display: block;
    position: relative;
    float: left;
    width: calc(100% - (50px*2));
    margin-left: 50px;
    margin-right: 50px;
    margin-bottom: 20px;
    > .subtitle{
      width: 100%;
      margin-top: 30px;
      margin-bottom: 30px;
      margin-left: 0;
      margin-right: 0;
    }
    &--field{
      display: block;
      position: relative;
      float: left;
      width: 100%;
      height: 42px;
      padding-left: 26px;
      padding-right: 26px;
      @include box-sizing(border-box);
      color: #0e0e0e;
      font-size: 14px;
      font-weight: 300;
      border: 1px solid #e4e4e4;
      border-radius: 32px;
      background-color: #fff;
      overflow: hidden;
    }
    &--submit{
      display: block;
      position: relative;
      float: left;
      width: 212px;
      height: 40px;
      margin-bottom: 34px;
      background-color: #71c438;
      border: none;
      border-radius: 19px;
      color: #fff;
      font-size: 14px;
      font-weight: 300;
      margin-top: 7px;
    }
    &--no-submit{
      display: block;
      position: relative;
      float: left;
      width: 212px;
      height: 40px;
      margin-bottom: 34px;
      margin-left: 20px;
      background-color: #ca3f43;
      border: none;
      border-radius: 19px;
      color: #fff;
      font-size: 14px;
      font-weight: 300;
      margin-top: 7px;
    }
  }
}
#black-overlay {
  display: none;
  z-index: 4;
  position: fixed;
  background-color: #000;
  opacity: 0.8;
  -moz-opacity: 0.8;
  filter: alpha(opacity=80);
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  cursor: pointer;
}